.services-description-info_open-pc {
  height: 360px; }

.services-description-info_desktop {
  overflow: hidden;
  height: 160px; }

.services-header {
  display: flex;
  justify-content: space-between; }
  .services-header__button {
    display: flex;
    padding: 0;
    vertical-align: middle;
    background: none;
    border: none;
    line-height: 40px; }
    .services-header__button:hover {
      transform: scale(1.05);
      transition: 0.3s ease-out;
      text-decoration: underline; }
    .services-header__button:after {
      content: '';
      width: 40px;
      height: 40px;
      margin-left: 8px;
      border-radius: 20px;
      background-color: #FF3E79; }
    .services-header__button:hover::after {
      -webkit-filter: saturate(3) brightness(1.2);
              filter: saturate(3) brightness(1.2);
      transition: 0.3s ease-out; }

@media (min-width: 768px) {
  .services-image {
    max-width: 360px;
    height: 100%; } }

@media (min-width: 1440px) {
  .services-image {
    max-width: 530px; } }

.services-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  overflow: scroll;
  width: auto;
  list-style: none; }
  .services-list__item {
    text-align: center;
    padding: 6px 16px; }
    .services-list__item:not(.services-list__item_active):hover {
      background-color: #EAEAEA;
      border-radius: 6px;
      text-decoration: underline; }
    .services-list__item_active {
      background-color: #FFFFFF;
      border: 2px solid #B8FFEC;
      border-radius: 6px; }
  @media (min-width: 768px) {
    .services-list {
      overflow: scroll;
      width: auto; } }
  @media (min-width: 1440px) {
    .services-list {
      flex-wrap: wrap;
      width: 1070px;
      overflow: hidden; } }

.services-navigation__list {
  margin: 0 37px 0 24px;
  padding: 0; }
  @media (min-width: 768px) {
    .services-navigation__list {
      margin-right: 0; } }
  @media (min-width: 1440px) {
    .services-navigation__list {
      margin-left: 32px;
      margin-right: 0; } }

.services-navigation__link {
  text-decoration: none;
  white-space: nowrap;
  text-transform: none; }

@media (min-width: 768px) {
