.slide__container {
  margin: 16px;
  margin-right: 0; }

.slider__header {
  margin-top: 32px; }

.slider__heading {
  margin: 0;
  padding: 16px; }

.slider-header {
  background: linear-gradient(90deg, #F8F8F8 0%, #FFFFFF 100%);
  border-top: 1px solid #D9FFF5; }

.slider-link__arrow {
  background-image: url(./static/dropdown.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  width: 8px;
  height: 12px; }

.slider-link__arrow_bottom {
  align-self: flex-end;
  margin-bottom: 16px; }

.swiper-slide {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 240px;
  height: 72px;
  padding: 0 16px;
  box-sizing: border-box;
  background-color: #FFFFFF;
  border: 1px solid #EAEAEA;
  border-radius: 6px; }
  .swiper-slide.repair-slide {
    flex-direction: column;
    height: 160px;
    align-items: normal; }
  .swiper-slide.prices-slide {
    height: 200px;
    flex-direction: column;
    align-items: flex-start;
    padding: 16px; }
  @media (min-width: 768px) {
    .swiper-slide.brands-section__slide {
      margin: 0 0 16px 24px; } }
  @media (min-width: 1440px) {
    .swiper-slide.brands-section__slide {
      margin-left: 32px; } }
  .swiper-slide.repair-section__slide {
    margin: 16px;
    margin-right: 0; }
    @media (min-width: 768px) {
