        margin: 24px 0 0 24px; } }
    @media (min-width: 1440px) {
      .swiper-slide.repair-section__slide {
        margin-left: 32px; } }
  .swiper-slide:hover {
    box-shadow: 0 0 5px 0 #7E7E82; }
    .swiper-slide:hover .brand-link {
      -webkit-filter: saturate(3) brightness(1.2);
              filter: saturate(3) brightness(1.2);
      transform: scale(1.1);
      transition: 0.4s ease-out; }
  @media (min-width: 768px) {
